import interfaces from '../../../../utils/interface.js';
export default {
	data() {
		return {
			show_buy: false,
			price: 999,
			jifen: 0, 
			curId: 0,
			page: 0, //当前所在页
			img: "../../../static/img/bg_liwu.jpg", //默认背景图
			img_on: "../../../static/img/bg_liwu_on.jpg", //选中背景图
			iconList: [{
				"id": "1",
				"jifen": "1500",
				"desc": "15元礼品包",
				"price": "12",
				"old_price": "15元"
			}, {
				"id": "2",
				"jifen": "2730",
				"desc": "39元礼品包",
				"price": "39"
			}, {
				"id": "3",
				"jifen": "7920",
				"desc": "99元礼品包",
				"price": "99"
			}, {
				"id": "4",
				"jifen": "17910",
				"desc": "199元礼品包",
				"price": "199"
			}, {
				"id": "5",
				"jifen": "29900",
				"desc": "299元礼品包",
				"price": "299"
			}, {
				"id": "6",
				"jifen": "59900",
				"desc": "599元礼品包",
				"price": "569",
				"old_price": "599元"

			}, {
				"id": "7",
				"jifen": "99900",
				"desc": "999元礼品包",
				"price": "899",
				"old_price": "999元"
			}, {
				"id": "8",
				"jifen": "199900",
				"desc": "1999元礼品包",
				"price": "1759",
				"old_price": "1999元"
			}, {
				"id": "9",
				"jifen": "399900",
				"desc": "3999元礼品包",
				"price": "3519",
				"old_price": "3999元"
			}, {
				"id": "10",
				"jifen": "599900",
				"desc": "5999元礼品包",
				"price": "5099",
				"old_price": "5999元"
			}]
		}
	},
	methods: {
		checked(e) { //点击选中礼包
			this.curId = e.id;
			this.price = e.price;
			this.jifen = e.jifen;
			this.show_buy = true;
		},
		changeCurrent(e) { //滑动swiper触发
			this.page = e.target.current;
		},
		navTo_lipin(){
			console.log('跳转')
			uni.navigateTo({
				url: '../swop'
			})
		},
		weixinPay(){
			//购买礼包
				uni.login({
					success: (e) => {
						console.log(e.code)
						uni.request({
							url: interfaces.jifen_buy,
							method: 'POST',
							data: {
								code: e.code, // code 换取 openid 和 session_key 等信息
								price: this.price, //支付价格
								jifen: this.jifen,//所获积分
							},
							success: (res) => {
								console.log(res, '成功');
								if (res.statusCode !== 200) {
									uni.showModal({
										content: "支付失败，请重试！",
										showCancel: false
									});
									return;
								}
								if (res.data.state === 200) {
									// console.log("得到接口prepay_id", res.data.payment);
									let paymentData = res.data;
									uni.requestPayment({
										timeStamp: paymentData.timeStamp,
										nonceStr: paymentData.nonceStr,
										package: paymentData.package,
										signType: 'MD5',
										paySign: paymentData.paySign,
										success: (res) => {
											uni.showToast({
												title: "感谢您的赞助!"
											})
										},
										fail: (res) => {
											uni.showModal({
												content: "支付失败,原因为: " + res
													.errMsg,
												showCancel: false
											})
										},
										complete: () => {
					
										}
									})
								} else {
									uni.showModal({
										content: res.data.desc,
										showCancel: false
									})
								}
							},
							fail: (e) => {
								console.log("fail错误", e);
								uni.showModal({
									content: "支付失败,原因为: " + e.errMsg,
									showCancel: false
								})
							}
						})
					},
					fail: (e) => {
						console.log("fail", e);
						uni.showModal({
							content: "支付失败,原因为: " + e.errMsg,
							showCancel: false
						})
					}
				})
			},
	},
	computed: {
		filterImgs() {
			let imgs = [];
			// let self = this;
			this.iconList.forEach((item, index) => {
				let page = ~~(index / 6) //~~ 取正
				if (!imgs[page]) { //如果page等于0; 取反则为true
					imgs[page] = []
				}
				imgs[page].push(item)
			})
			return imgs;
		}
	}
}
